:root {
	--primary: #F6A421;
	/* --primary: #FF4D4F; */
	--secondary-color: #e20000;
	--text-dark: #222;
	--white: #fff;
	--card-radius: 30px;
	--muted: #6b7280;
	--heading: #002137;
	--sub-heading: #00406c;
	--text: #6b7280;
	--font-family: "Poppins", sans-serif;
}

html,
body {
	margin: 0;
	padding: 0;
	height: auto;
	font-family: "Montserrat", sans-serif;
	background: #f5f5f5;
}

a {
	text-decoration: none;
	color: #f9f9f9;
}

/* Button */
button, .btn-primary {
	width: fit-content;
	background-color: var(--primary);
	color: hsl(0, 0%, 100%);
	padding: 0.55rem 1rem;
	border-radius: 0.25rem;
	border: none;
	cursor: pointer;
	font-weight: 500;
	font-size: 16px;
	transition: background-color 0.3s ease;
}

button:hover {
	background-color: var(--primary);
}

.App {
	text-align: center;
	background: #ffffff;
}

.row {
	display: flex;
	flex-wrap: wrap;
	gap: 15px;
	width: 100%;
	margin-bottom: 40px;
}

.col {
	flex: 1;
	padding-left: 15px;
	padding-right: 15px;
}

/* Fixed column sizes (12-column grid like Bootstrap) */
.col-1 {
	flex: 0 0 8.3333%;
	max-width: 8.3333%;
}

.col-2 {
	flex: 0 0 16.6666%;
	max-width: 16.6666%;
}

.col-3 {
	flex: 0 0 25%;
	max-width: 25%;
}

.col-4 {
	flex: 0 0 33.3333%;
	max-width: 32%;
}

.col-5 {
	flex: 0 0 41.6666%;
	max-width: 41.6666%;
}

.col-6 {
	flex: 0 0 50%;
	max-width: 50%;
}

.col-7 {
	flex: 0 0 58.3333%;
	max-width: 58.3333%;
}

.col-8 {
	flex: 0 0 66.6666%;
	max-width: 66.6666%;
}

.col-9 {
	flex: 0 0 75%;
	max-width: 75%;
}

.col-10 {
	flex: 0 0 83.3333%;
	max-width: 83.3333%;
}

.col-11 {
	flex: 0 0 91.6666%;
	max-width: 91.6666%;
}

.col-12 {
	flex: 0 0 100%;
	max-width: 100%;
}

/* Responsive breakpoints */
@media (max-width: 992px) {
	.col-lg-6 {
		flex: 0 0 50%;
		max-width: 50%;
	}

	.col-lg-12 {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

@media (max-width: 768px) {
	.col-md-6 {
		flex: 0 0 50%;
		max-width: 50%;
	}

	.col-md-12 {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

@media (max-width: 576px) {
	.col-sm-12 {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

.container {
	height: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 3rem 9rem;
}

.card {
	min-width: auto;
	/* prevent getting too small */
	display: flex;
	flex-direction: column;
	justify-content: center;
	/* align-items: center; */
	background-color: white;
	box-shadow: 0px 10px 20px 0px lightgrey;
	border-radius: 10px;
	padding: 10px 40px;
}

.card-body {
	padding: 15px 0px;
	width: 100%;
}

.group-input {
	display: flex;
	flex-direction: column;
	margin-bottom: 15px;
}

label {
	font-size: 16px;
	font-weight: 400;
	color: grey;
	margin-bottom: 10px;
}

input,
textarea,
select {
	width: 100%;
	padding: 10px 12px;
	margin: 8px 0;
	box-sizing: border-box;
	border: 1px solid #cbd5e1;
	border-radius: 6px;
	font-size: 16px;
	transition: border 0.2s, box-shadow 0.2s;
}

input:focus {
	border: 1px solid #e04344 !important;
}

.checkbox {
	display: flex;
	align-items: center;
	gap: 8px;
	font-size: 0.95rem;
	color: var(--muted);
}

.toggelBtn {
	position: absolute;
	right: 10px;
	top: 50%;
	transform: translateY(-50%);
	cursor: pointer;
	color: #555;
}

.page-heading {
	font-size: 26px;
	margin: 10px 0px;
	color: var(--muted)
}

@media (max-width:768px){
	.container {
		padding: 3rem 1rem;
	}
}



/*================Frontend================== */
/* Hero section style */
section.hero {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: start;
	justify-content: center;
	height: 100vh;
	overflow: hidden;
}

.hero::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100%;
	background: url('./images/hero_banner.jpg');
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	transform: scale(1);
	transition: transform 8s ease-in-out;
	z-index: 1;
}

section.hero::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: rgba(0, 0, 0, 0.70);
	/* dark overlay */
	z-index: 2;
}

/* Zoom-in animation on hover or auto */
.hero:hover::before {
	transform: scale(1.1);
}

/* You can also make it auto-zoom by using animation */
.hero::before {
	animation: zoomIn 20s ease-in-out infinite alternate;
}

/* Keyframes for smooth zoom-in and zoom-out */
@keyframes zoomIn {
	from {
		transform: scale(1);
	}

	to {
		transform: scale(1.15);
	}
}

/*============= hero section header style =============== */
.header {
	position: fixed;
	top: 0px;
	left: 0px;
	width: 80%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1.5rem 9rem;
	background: transparent;
	transition: background 0.5s ease, box-shadow 0.5s ease;
	z-index: 3;
}

.common.header .logo {
	font-size: 1.5rem;
	font-weight: 700;
	height: 40px;
	width: 120px;
}

.header .links a {
	color: var(--primary);
	text-decoration: none;
	padding: 10px 25px;
	transition: color 0.3s ease;
}

.common.header .links a {
	color: #1A1A1A;
	font-weight: 500;
}

.header .links a:hover {
	color: var(--primary);
}
.menu-icon {
    display: none;
    flex-direction: column;
    gap: 4px;
    cursor: pointer;
}

.menu-icon span {
    width: 28px;
    height: 3px;
	border-radius: 10px;
    background: var(--primary);
}



/* Mobile styles */
@media(max-width: 768px) {
	.header {
		z-index: 4;
	}

	.links {
		position: absolute;
		top: 0;
		left: 0;
		width: 352px;
		height: 100vh;
		background: #1A1A1A;
		display: flex;
		flex-direction: column;
		padding: 50px 20px 20px;
		gap: 10px;
		box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

		transform: translateX(100%);
		opacity: 0;
		transition: transform 0.4s ease, opacity 0.3s ease;
	}

	.links.show {
		transform: translateX(0);
		opacity: 1;
	}

	.menu-icon {
		top: 25px;
		left: 340px;
		position: absolute;
		display: flex;
		z-index: 5;
	}
}


