/* =====================Login style====================  */
/* Container */
.login-container {
	display: flex;
	width: 900px;
	height: 500px;
	margin: 50px auto;
	background: #fff;
	border-radius: 20px;
	overflow: hidden;
	box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
}

/* Left section */
.left-section {
	flex: 1;
	background: linear-gradient(180deg, #f9f9f9 0%, #fff7e0 100%);
	padding: 50px;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.left-section h2 {
	margin: 0;
	font-size: 26px;
	font-weight: 700;
	color: #333;
}

.left-section p {
	margin: 8px 0 25px;
	color: #666;
	font-size: 14px;
}

.input-field {
	width: 100%;
	padding: 14px;
	margin-bottom: 15px;
	border: none;
	outline: none;
	border-radius: 25px;
	background: #f4f5f7;
	font-size: 14px;
	transition: 0.3s;
}

.input-field:focus {
	border: 2px solid #ffd54f;
	background: #fff;
}

/* Right section */
.right-section {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
}
.right-section::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	height: 65vh;
	width: 100%;
	background: url('./images/auth.png');
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	transform: scale(1);
	transition: transform 8s ease-in-out;
	z-index: 1;
}

.right-section::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: rgba(0, 0, 0, 0.70);
	/* dark overlay */
	z-index: 2;
}

/* Hanging text */
.hanging-text {
	display: flex;
	gap: 25px;
	z-index: 4;
}

.hanging-text span {
	font-size: 80px;
	font-weight: 800;
	color: #ffd54f;
	position: relative;
	display: inline-block;
	transform-origin: top center;
	transition: transform 0.3s ease-in-out;
}

/* Swing on hover */
.hanging-text span:hover {
	cursor: pointer;
	animation: swing 1s ease-in-out infinite alternate;
}

/* Swing animation */
@keyframes swing {
	0% {
		transform: rotate(10deg);
	}

	50% {
		transform: rotate(-10deg);
	}

	100% {
		transform: rotate(10deg);
	}
}

@media (max-width:768px) {
	.login-container {
		flex-direction: column;
        height: 100vh;
        width: 100%;
        margin: 0px;
        border-radius: 0px;
	}
	.left-section {
		flex: none;
	}
	.right-section::before {
		height: 47vh;
	}
}
