}












/* ====================admin panel====================== */
.admin-container {
	display: flex;
	height: 100vh;
}

.main-content {
	flex: 1;
	margin-left: 270px;
	padding-left: 25px;
	display: flex;
	flex-direction: column;
	width: calc(100% - 295px);

}

.main-container {
	height: calc(100vh - 80px);
	display: flex;
	justify-content: start;
	flex-direction: column;
	width: calc(100% - 25px);
}
.main-container .footer{
	padding: 40px 0 ;
}

.sidebar {
	width: 270px;
	top: 0;
	left: 0;
	position: fixed;
	display: flex;
	justify-content: start;
	flex-direction: column;
	height: 100vh;
	background: linear-gradient(180deg, #fff 30%, #fff8e1 70%);
	border-radius: 0 25px 25px 0px;
	transition: transform 0.4s ease;
}

.sidebar-logo {
	border-bottom: 1px solid #333;
}

.sidebar-logo .logo {
	padding: 20px 65px;
	height: 40px;
	width: 120px;
	align-items: center;
}

.sidebar-menu {
	list-style: none;
	padding: 0;
	margin: 20px 0;
}

.sidebar-menu li {
	padding: 15px 25px;
	cursor: pointer;
	font-size: 16px;
	transition: background 0.3s;
}

.icon {
	padding-right: 20px;
	font-size: 24px;
}

.item-link {
	text-decoration: none;
	color: grey;
	text-align: center;
}

.navbar {
	height: 80px;
	background: white;
	display: flex;
	border-radius: 0px 0px 0px 25px;
	justify-content: space-between;
	align-items: center;
	position: sticky;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
	padding: 0px 25px;
	top: 0px;
}

.sidebar-menu-icon {
    display: none;
    flex-direction: column;
    gap: 4px;
    cursor: pointer;
}

.sidebar-menu-icon span {
    width: 28px;
    height: 3px;
	border-radius: 10px;
    background: var(--primary);
}
@media (max-width:768px){
	.sidebar {
		left: 105px;
        transform: translateX(100%);
    }

    .sidebar.open {
		border-radius:  25px 0 0 25px;
		z-index: 6;
        transform: translateX(0);
    }

	.sidebar-menu-icon {
        position: fixed;
        flex-direction: column;
        gap: 5px;
        cursor: pointer;
		top: 21px;
        left: 324px;
        display: flex;
        z-index: 7;
    }

	.main-content {
		margin: 0;
		padding: 0;
	}
	/* Backdrop */
    .sidebar-backdrop {
        position: fixed;
        inset: 0;
        background: rgba(0, 0, 0, 0.4);
        z-index: 5;
    }
	.navbar{
		display: flex;
		height: 55px;
		border-radius: 0;
	}
}


.booking-page {
	max-width: 1000px;
	margin: 30px auto;
	padding: 20px;
	font-family: "Poppins", sans-serif;
	color: #1e293b;
}

.title {
	font-size: 1.8rem;
	text-align: start;
	margin-bottom: 2px;
}

.subtitle {
	text-align: start;
	color: #64748b;
	margin-top: 10px;
	margin-bottom: 30px;
}

/* Booking management */
.barber-section {
	background-color: var(--white);
	padding: 20px;
	margin-bottom: 40px;
}
.barber-section h3,
.manage-section h3 {
	margin-bottom: 10px;
	color: var(--heading);
	font-size: 2.2rem;
	margin: 0px;
}
.barber-section h5, .barber-section strong{
	margin: 2px;
    font-weight: 600;
    color: var(--sub-heading);
    font-size: 18px;
}
.slots.card, .services .card{
	min-width:0px;
	cursor: pointer;
}
.slots.card.selected, 
.services .card.selected{
	background-color: var(--primary);
	color: var(--white);
}
.slots.card.disabled{
	background-color: lightgrey;
	color: var(--text-dark);
}
.booking .services {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	gap: 20px;
	margin-bottom: 40px;
}

.booking .services .card {
	padding: 1rem;
	/* background-image: url("images/service-card.webp"); */
}

.booking .services .card .card-header {
	text-align: start;
	font-size: 20px;
	font-weight: 600;
}

.price {
	text-align: start;
}



.barber-list {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
	gap: 16px;
}

.barber-card {
	background: #f8fafc;
	border: 1px solid #e2e8f0;
	border-radius: 10px;
	padding: 15px;
	transition: 0.3s;
	cursor: pointer;
}

.barber-card:hover {
	transform: scale(1.02);
	background: #f1f5f9;
}

.barber-card.selected {
	border: 2px solid #2563eb;
	background: #e0f2fe;
}

.booking-form {
	margin: 40px 0;
	padding: 20px;
}

.form-grid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
	gap: 12px;
	margin-top: 15px;
}





.manage-section {
	margin-top: 40px;
}

.bookings-list {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	gap: 16px;
	margin-top: 10px;
}

.booking-card {
	background: #fff;
	border: 1px solid #e2e8f0;
	border-radius: 10px;
	padding: 15px;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}
.booking-card .row{
	margin: 0px;
}
.booking-card .danger {
	/* margin-top: 8px; */
	background: #ef4444;
	color: #fff;
	border: none;
}

.booking-card button:hover {
	background: #dc2626;
}






